import {Group} from '../../entities/group';
import {Action, Selector, State, StateContext} from '@ngxs/store';
import {Guid} from '../../../tests/helpers/guid';
import {Channel} from '../../entities/channel';
import {User} from '../../entities/user';
import {
  AddMessage,
  CreateLocalGroup,
  CreateNewChannel,
  SaveMessageToChannel,
  SetCurrentChannel,
  SetCurrentGroup,
  SubscribeToGroup
} from './app-state.actions';
import {Message} from '../../entities/message';
import {GroupType} from '../../enums/group-type.enum';
import {UserDataStore} from './DataStore/user-data-store';
import {UiState} from './ui-state';
import {MessageVm} from '../../view-models/message-vm';
import {GroupInfoVm} from '../../view-models/group-info-vm';

export interface AppStateModel {
  dataStore: UserDataStore;
  uiState: UiState;
}

const constUser: User = new User('e51636d8-2a9d-43bf-8f67-be9c1146ec99', 'ich');
const constUser2: User = new User('df6ed3e6-82ed-4a1b-b75b-d9699d9ce1f5', 'Max');

const channels = new Array<Channel>(...[
  new Channel(Guid.newGuid(), 'Allgemein', [
    new Message(Guid.newGuid(), 'Message Allgemein 1', new Date(Date.now()), constUser.id),
    new Message(Guid.newGuid(), 'Message Allgemein 2', new Date(Date.now()), constUser2.id),
    new Message(Guid.newGuid(), 'Message Allgemein 3', new Date(Date.now()), constUser.id)
  ]),
  new Channel(Guid.newGuid(), 'Drop Treffen', [
    new Message(Guid.newGuid(), 'Message Drop Treff 1', new Date(Date.now()), constUser.id),
    new Message(Guid.newGuid(), 'Message Drop Treff 2', new Date(Date.now()), constUser2.id),
    new Message(Guid.newGuid(), 'Message  Drop Treff 3', new Date(Date.now()), constUser.id)
  ]),
  new Channel(Guid.newGuid(), 'Stammtisch', []),

]);

function setParticipanst(channesl: Channel[], groupId: string) {
  channesl.forEach(x => {
    const userIds = new Set<string>();
    x.messages.forEach(m => userIds.add(m.userId));
    x.participantIds = Array.from(userIds.values());
    x.groupId = groupId;
  });
}

const group2Channels: Channel[] = [
  new Channel(Guid.newGuid(), 'Allgemein', [
    new Message(Guid.newGuid(), 'Message Drop Treff 1', new Date(Date.now()), constUser.id),
    new Message(Guid.newGuid(), 'Message Drop Treff 2', new Date(Date.now()), constUser2.id),
    new Message(Guid.newGuid(), 'Message  Drop Treff 3', new Date(Date.now()), constUser.id)
  ])
];

const group: Group = new Group(Guid.newGuid(), 'VCA Karlsruhe', GroupType.Event, channels);
const group2 = new Group(Guid.newGuid(), 'VCA Stuttgart', GroupType.LocalGroup);

const group3Channels: Channel[] = [
  new Channel(Guid.newGuid(), 'Allgemein')
];
const group3 = new Group(Guid.newGuid(), 'VCS St.P', GroupType.LocalGroup, []);


group.participantIds.push(...[constUser.id, constUser2.id]);
group2.participantIds.push(...[constUser.id, constUser2.id]);

setParticipanst(channels, group.id);
setParticipanst(group2Channels, group2.id);
setParticipanst(group3Channels, group3.id);

@State<AppStateModel>({
  name: 'appState',
  defaults: {
    dataStore: {
      groups: new Map<string, Group>([
        [group.id, group],
        [group2.id, group2],
        [group3.id, group3],
      ]),
      channels: new Map<string, Channel>([
        [channels[0].id, channels[0]],
        [channels[1].id, channels[1]],
        [group2Channels[0].id, group2Channels[0]],
        [group3Channels[0].id, group3Channels[0]]
      ]),
      messages: new Map<string, Message[]>([
        [channels[0].id, channels[0].messages],
        [channels[1].id, channels[1].messages],
        [group2Channels[0].id, group2Channels[0].messages],
      ]),
      users: new Map<string, User>([
        [constUser.id, constUser],
        [constUser2.id, constUser2],
      ])
    },
    uiState: {
      userId: constUser.id,
      currentGroupId: group.id,
      currentChannelId: channels[0].id,
      lastVisitChannel: new Map<string, string>(),
      channelMessage: new Map<string, string>()
    }
  },
  children: []
})
export class AppState {


  constructor() {
  }

  @Selector()
  static currentChannel(state: AppStateModel): Channel {
    return state.dataStore.channels.get(state.uiState.currentChannelId);
  }

  @Selector()
  static currentGroup(state: AppStateModel): Group {
    return state.dataStore.groups.get(state.uiState.currentGroupId);
  }

  @Selector()
  static subscribedChannels(state: AppStateModel): Channel[] {
    return Array.from(state.dataStore.channels.values())
      .filter(x => x.participantIds.includes(state.uiState.userId)
        && x.groupId === state.uiState.currentGroupId);
  }

  @Selector()
  static subscribedGroups(state: AppStateModel): GroupInfoVm[] {
    return Array.from(state.dataStore.groups.values()).filter(x => x.participantIds.includes(state.uiState.userId)).map<GroupInfoVm>(g => {
      return {
        id: g.id,
        name: g.name,
        groupType: g.groupType,
      } as GroupInfoVm;
    });
  }

  @Selector()
  static notSubscribedGroups(state: AppStateModel): GroupInfoVm[] {
    const userid = state.uiState.userId;
    return Array.from(state.dataStore.groups.values()).filter(x => !x.participantIds.includes(userid)).map<GroupInfoVm>(g => {
      return {
        id: g.id,
        name: g.name,
        groupType: g.groupType,
      } as GroupInfoVm;
    });
  }

  @Selector()
  static user(state: AppStateModel): User {
    return state.dataStore.users.get(state.uiState.userId);
  }

  @Selector()
  static currentChatMessages(state: AppStateModel): MessageVm[] {
    const mapToVM = (message: Message): MessageVm => {
      const name = state.dataStore.users.has(message.userId) ? state.dataStore.users.get(message.userId).userName : '';
      return {
        id: message.id,
        content: message.content,
        timestamp: message.createdOn.getDate(),
        userName: name
      } as MessageVm;
    };

    const messages = state.dataStore.messages.get(state.uiState.currentChannelId).map<MessageVm>(x => mapToVM(x));
    console.log(messages);
    return messages;
  }

  @Selector()
  static currentChatMessage(state: AppStateModel): string {
    if (state.uiState.channelMessage.has(state.uiState.currentChannelId)) {
      return state.uiState.channelMessage.get(state.uiState.currentChannelId);
    }
    return '';
  }

  @Action(SetCurrentChannel)
  setCurrentChannel(ctx: StateContext<AppStateModel>, action: SetCurrentChannel) {
    const currentUiState = ctx.getState().uiState;
    currentUiState.currentChannelId = action.channelId;
    currentUiState.lastVisitChannel.set(currentUiState.currentGroupId, action.channelId);
    ctx.patchState({
      uiState: currentUiState,
    });
  }

  @Action(CreateNewChannel)
  createNewChannel(ctx: StateContext<AppStateModel>, action: CreateNewChannel) {
    const currentState = ctx.getState();

    const newChannel = new Channel(Guid.newGuid(), action.name);
    newChannel.groupId = currentState.uiState.currentGroupId;
    newChannel.participantIds.push(currentState.uiState.userId);
    newChannel.participantIds.push(...action.participantsIds);
    currentState.dataStore.channels.set(newChannel.id, newChannel);
    currentState.uiState.currentChannelId = newChannel.id;
    ctx.patchState({
      dataStore: currentState.dataStore,
      uiState: currentState.uiState
    });
  }

  @Action(SetCurrentGroup)
  setCurrentGroup(ctx: StateContext<AppStateModel>, action: SetCurrentGroup) {
    const currentState = ctx.getState();
    currentState.uiState.currentGroupId = action.groupId;
    if (currentState.uiState.lastVisitChannel.has(action.groupId)) {
      currentState.uiState.currentChannelId = currentState.uiState.lastVisitChannel.get(action.groupId);
    } else {
      const groupChannels = Array.from(currentState.dataStore.channels.values()).filter(x => x.groupId === action.groupId);
      if (groupChannels.length > 0) {
        currentState.uiState.currentChannelId = groupChannels[0].id;
      } else {
        currentState.uiState.currentChannelId = null;
      }
    }
    ctx.patchState({
      uiState: currentState.uiState
    });
  }

  @Action(SubscribeToGroup)
  subscribeToGroup(ctx: StateContext<AppStateModel>, action: SubscribeToGroup) {
    const state = ctx.getState();
    const dataStore = state.dataStore;
    const groupToSubscribe = dataStore.groups.get(action.groupId);
    groupToSubscribe.participantIds = [...groupToSubscribe.participantIds, state.uiState.userId];
    ctx.patchState({
      dataStore: dataStore
    });
  }


  @Action(CreateLocalGroup)
  createLocalGroup(ctx: StateContext<AppStateModel>, action: CreateLocalGroup) {
    const state = ctx.getState();

    const newGroup = new Group(Guid.newGuid(), action.name, GroupType.LocalGroup);
    newGroup.description = action.description;
    newGroup.participantIds.push(state.uiState.userId);
    state.dataStore.groups.set(newGroup.id, newGroup);

    state.uiState.currentGroupId = newGroup.id;
    ctx.patchState({
      uiState: state.uiState,
      dataStore: state.dataStore
    });
    ctx.dispatch( new CreateNewChannel('Allgemein'));
  }

  @Action(AddMessage)
  addMessage(ctx: StateContext<AppStateModel>, action: AddMessage) {
    const state = ctx.getState();
    const dataStore = state.dataStore;
    let messages: Message[] = [];

    if (dataStore.messages.has(state.uiState.currentChannelId)) {
      messages = dataStore.messages.get(state.uiState.currentChannelId);
    }

    dataStore.messages.set(state.uiState.currentChannelId,
      [
        ...messages, new Message(Guid.newGuid(), action.content, new Date(Date.now()), state.uiState.userId)
      ]);
    ctx.patchState({
      dataStore: dataStore
    });
  }

  @Action(SaveMessageToChannel)
  updateCurrentMessage(ctx: StateContext<AppStateModel>, action: SaveMessageToChannel) {
    const currentUiState = ctx.getState().uiState;
    currentUiState.channelMessage.set(action.channelId, action.content);
    ctx.patchState({
      uiState: currentUiState
    });
  }


}



