
export class Message {
  public id: string;
  public userId: string;
  public content: string;
  public createdOn: Date;


  constructor(messageId: string, content: string, createdOn: Date, userId: string) {
    this.id = messageId;
    this.content = content;
    this.createdOn = createdOn;
    this.userId = userId;
  }
}
