import {Component, OnInit} from '@angular/core';
import {Select, Store} from '@ngxs/store';
import {AppState} from '../../../shared/states/app-state/app-state';
import {Observable} from 'rxjs';
import {GroupInfoVm} from '../../../shared/view-models/group-info-vm';
import {SetCurrentGroup} from '../../../shared/states/app-state/app-state.actions';
import {Group} from '../../../shared/entities/group';
import {ModalController} from '@ionic/angular';
import {SubscribeToGroupModalComponent} from '../../modals/subscribe-to-group-modal/subscribe-to-group-modal.component';

@Component({
  selector: 'app-group-navigation-menu',
  templateUrl: './group-navigation-menu.component.html',
  styleUrls: ['./group-navigation-menu.component.scss'],
})
export class GroupNavigationMenuComponent implements OnInit {


  @Select(AppState.subscribedGroups) subscribedGroups$: Observable<GroupInfoVm[]>;
  @Select(AppState.currentGroup) currentGroup$: Observable<Group>;

  constructor(private store: Store, public modalController: ModalController) {
  }

  ngOnInit() {
  }

  selectGroup(group: GroupInfoVm) {
    this.store.dispatch(new SetCurrentGroup(group.id));
  }

  identifyGroup(index: number, item: GroupInfoVm) {
    return item.id;
  }

  async presentSubscribeToGroupModal(){
    const modal = await this.modalController.create({
      component: SubscribeToGroupModalComponent
    });
    return await modal.present();
  }

}
