import {Component, OnInit} from '@angular/core';
import {Select, Store} from '@ngxs/store';
import {AppState} from '../../../shared/states/app-state/app-state';
import {Observable} from 'rxjs';
import {GroupInfoVm} from '../../../shared/view-models/group-info-vm';
import {SubscribeToGroup} from '../../../shared/states/app-state/app-state.actions';
import {ModalController} from '@ionic/angular';
import {CreateGroupModalComponent} from '../create-group-modal/create-group-modal.component';

@Component({
  selector: 'app-subscribe-to-group-modal',
  templateUrl: './subscribe-to-group-modal.component.html',
  styleUrls: ['./subscribe-to-group-modal.component.scss'],
})
export class SubscribeToGroupModalComponent implements OnInit {

  @Select(AppState.notSubscribedGroups) notSubscribedGroups$: Observable<GroupInfoVm[]>;

  filteredGroups: GroupInfoVm[];
  groups: GroupInfoVm[];

  constructor(private store: Store, private modalController: ModalController) {
  }

  ngOnInit() {
    this.notSubscribedGroups$.subscribe(x => {
      this.filteredGroups = x;
      this.groups = x;
    });
  }

  filterList(event: CustomEvent) {
    this.filteredGroups = this.groups;

    if (!event.detail && !event.detail.target) {
      return;
    }
    const search = event.detail.target.value;

    if (search) {
      return;
    }

    this.filteredGroups = this.filteredGroups.filter(group => {
      if (group && search) {
        return group.name.toLowerCase().indexOf(search.toLowerCase()) > -1;
      }
    });
  }

  subscribeToGroup(groupId: string) {
    this.store.dispatch(new SubscribeToGroup(groupId));
    this.dismissModal();
  }

  dismissModal() {
    this.modalController.dismiss();
  }

  async createNewGroup() {
    const modal = await this.modalController.create({
      component: CreateGroupModalComponent
    });

    modal.onWillDismiss().then(x => {
      if (!x.data.dismissed) {
        this.dismissModal();
      }
    });

    return await modal.present();
  }
}
