import {Component, ViewChild} from '@angular/core';

import {IonSlides, MenuController, Platform} from '@ionic/angular';
import {SplashScreen} from '@ionic-native/splash-screen/ngx';
import {StatusBar} from '@ionic-native/status-bar/ngx';
import {Select, Store} from '@ngxs/store';
import {AppState} from '../shared/states/app-state/app-state';
import {Observable} from 'rxjs';
import {Group} from '../shared/entities/group';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent {

  @Select(AppState.currentGroup) currentGroup$: Observable<Group>;
  @ViewChild('switcher', {static: true}) switcher: IonSlides;

  public slideConfig = {
    initialSlide: 1,
    speed: 0,
    autoHeight: true,
  };

  public slideStates: boolean[] = [false, true];

  public appPages = [
    {
      title: 'Home',
      url: '/home',
      icon: 'home'
    },
    {
      title: 'List',
      url: '/list',
      icon: 'list'
    }
  ];

  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private store: Store,
    private menu: MenuController
  ) {
    this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }





  menuDidOpen(event: CustomEvent) {
    this.menu.swipeGesture(false, 'main-menu');
  }

  menuDidClose(event: CustomEvent) {
    this.menu.swipeGesture(true, 'main-menu');
  }

  menuWillOpen(event: CustomEvent) {
    this.switcher.update();
  }

  identifyBullet(index: number, item: boolean) {
    return index;
  }

  slideTo(index: number) {
    this.switcher.slideTo(index, 300);
    this.updateSlideStates(index);
  }

  slideDidChange(event: CustomEvent) {
    this.switcher.getActiveIndex().then(index => {
      this.updateSlideStates(index);
    });

  }

  private updateSlideStates(index: number) {
    this.slideStates = this.slideStates.fill(false);
    this.slideStates[index] = true;
  }

}
