import {Component, OnInit} from '@angular/core';
import {Select, Store} from '@ngxs/store';
import {AppState} from '../../../shared/states/app-state/app-state';
import {Observable} from 'rxjs';
import {Channel} from '../../../shared/entities/channel';
import {SetCurrentChannel} from '../../../shared/states/app-state/app-state.actions';

@Component({
  selector: 'app-channel-navigation-menu',
  templateUrl: './channel-navigation-menu.component.html',
  styleUrls: ['./channel-navigation-menu.component.scss'],
})
export class ChannelNavigationMenuComponent implements OnInit {

  @Select(AppState.subscribedChannels) subscribedChannels$: Observable<Channel[]>;
  @Select(AppState.currentChannel) currentChannel$: Observable<Channel>;


  constructor(private store: Store) { }

  ngOnInit() {}

  selectChannel(channel: Channel) {
    this.store.dispatch(new SetCurrentChannel(channel.id));
  }

  identifyChannel(index: number, item: Channel){
    return item.id;
  }

}
